@media (max-width: 990px) {
    .header-head {
        gap: 0rem;
    }
    .maps-select-header {
        font-size: 2.5rem;
    }
    .header-head-maps {
        height: 35vh;
        align-items: baseline;
    }
    .maps-select {
        gap: 1rem;
    }
    .map-select-body-image {
        object-fit: cover;
        height: 100%;
    }
    .slide-down {
        width: 30%;
    }
    .coords {
        justify-content: center;
        align-items: center;
    }
    .coords-text {
        font-size: 1.3rem;
    }
    .note {
        font-size: 1.5rem;
        align-items: center;
        justify-content: center;
    }
    .pane {
        padding-right: 2rem;
    }
    .position-form {
        justify-content: center;
        gap: 2rem;
    }
    .text-input {
        width: 30vw;
    }
    .submitButton {
        font-size: 1.5rem;
        outline-width: 1px;
        outline-offset: 0.2rem;
        outline-color: white;
        box-shadow: none;
    }
    .info-box-paragraph {
        width: 80%;
        font-size: 1.9rem;
    }
    .credit {
        /* padding-top: 0.1rem; */
        height: 2rem;
        font-size: 1rem;
        line-height: 1.5;
    }
}
@media (max-width: 700px) {
    .map {
        width: 40vw;
    }
    .panel {
        width: 60vw;
    }
    .pane {
        height: 4rem;
    }
    .delete-button {
        justify-self: center;
        height: 3rem;
    }
    .panel-get-position {
        font-size: 2rem;
        padding-top: 2vh;
    }
    .info-box {
        width: 80vw;
        border-top-left-radius: 7rem;
        border-bottom-right-radius: 7rem;
    }
    .info-box-paragraph {
        width: 80%;
        font-size: 1.9rem;
    }
    .credit {
        /* padding-top: 0.1rem; */
        height: 2rem;
        font-size: 1rem;
        line-height: 1.5;
    }
}
@media (max-width: 600px) {
    .maps-select-header {
        font-size: 1.5rem;
        border-top-right-radius: 1rem;
        border-top-left-radius: 1rem;
        outline-offset: -0.4rem;
    }
    .info-button {
        font-size: 1.75rem;
        outline-offset: 0.3rem;
    }
    .pane {
        height: 3rem;
    }
    .coords-text {
        font-size: 1.1rem;
        font-weight: 400;
    }
    .note-text {
        font-size: 1.2rem;
    }
    .info-box {
        width: 90vw;
        height: 80vh;
    }
    .info-box-paragraph {
        font-size: 1.5rem;
        width: 90%;
    }
}
@media (max-width: 500px) {
    .body {
        flex-direction: column;
    }
    .map {
        height: 45vh;
        width: 100vw;
    }
    .panel {
        margin-top: 0;
        height: 55vh;
        width: 100vw;
        border-radius: 0;
        border-top-left-radius: 5rem;
        border-top-right-radius: 5rem;
        padding-top: 5vh;
        gap: 3vh;
        padding-left: 1rem;
        padding-right: 1rem;
    }
    .panel-get-position {
        height: 4rem;
        font-size: 2.5rem;
        width: 60vw;
        padding: 0;
        padding-top: 0.5rem;
        border-bottom-right-radius: 1rem;
        border-top-left-radius: 1rem;
        outline-offset: 0.3rem;
        outline-width: 1px;
    }
    .text-input {
        width: 60vw;
    }
    .submitButton {
        font-size: 1.5rem;
        outline-offset: 0.5rem;
    }
    .logs-header {
        font-size: 1.6rem;
        height: 3rem;
        /* padding-bottom: 0.3rem; */
    }
    .logs {
        justify-content: center;
        gap: 0;
    }
    .logs-body {
        height: 35vh;
        padding: 0;
        padding-left: 1rem;
        padding-right: 1rem;
        margin-top: 0;
    }
}
